.logo{
    display: block;
    height: 150px;
    width: 150px;
    position: relative;
    left: 45%;
    top: 100px ;
    animation-name: logotime;
    animation-duration: 2s;
    
}

@keyframes logotime{
    from {top: -20px;}
    to {top: 100px;}
}

.logoBack{
    display: none;
    height: 150px;
    width: 150px;
    position: relative;
    left: 50%;
    top: -1px;
}